@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {

  --color-primary: #204056;

  --color-primary-hover: #20405699;

  --color-secondary: #f65a5b;

  --color-secondary-hover: #f65a5bcc;

  --color-white: #fffd;

  --color-white-hover: #fff9;

}

* {
  box-sizing: border-box;
}

body {
  background-color: #204056;
  background-color: var(--color-primary);
  font-family: Roboto , sans-serif;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 980px;
  padding: 0 16px;
}

header {
  margin: 0 0 16px;
  padding: 16px 0;
  width: 100%;
}

header img {
  max-width: 200px;
}

header .container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0;
}

header .box-score {
  background-color: #fffd;
  background-color: var(--color-white);
  border-radius: 12px;
  box-shadow: 0 0 8px rgba(0 0 0 0.5);
  display: -webkit-flex;
  display: flex;
  min-width: 400px;
  padding: 8px 16px;
}

header .box-score .box-score-badges {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

header .box-score img {
  border-radius: 50%;
  margin-right: 12px;
  padding: 8px 0;
}

.login-container h1 {
  color: #f65a5b;
  color: var(--color-secondary);
  font-size: 2rem;
  text-align: center;
}

.login-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 32px;
}

.login-container form {
  -webkit-align-self: center;
          align-self: center;
  background-color: #f65a5b;
  background-color: var(--color-secondary);
  border-radius: 16px;
  max-width: 600px;
  padding: 48px 64px;
}

.login-container form input {
  width: 300px;
}

.login-container .Dropdown-root {
  margin-top: 16px;
}

.login-container .Dropdown-root .Dropdown-control {
  border-radius: 8px;
  padding: 12px;
}

.login-container .Dropdown-control:last-child {
  margin-bottom: 16px;
}

.login-container button:first-child {
  position: absolute;
  right: 32px;
  top: 32px;
}

.box-accent {
  background-color: #f65a5b;
  background-color: var(--color-secondary);
  border-radius: 16px;
  padding: 32px 64px;
}

.box-accent h2 {
  margin: 0;
  padding: 0 8px 8px 0;
}

.box-accent h2, .box-accent h1, .box-accent p {
  color: #fffd;
  color: var(--color-white);
}

.box-accent p {
  font-weight: 700;
}

.box-question-opt {
  display: -webkit-flex;
  display: flex;
}

.box-question-opt .exact {
  margin: 16px 8px 16px 0;
  text-transform: capitalize;
}

.box-feedback__flex {
  display: -webkit-flex;
  display: flex;
}

.box-feedback h1 {
  margin-top: 0;
}

.box-feedback .box-feedback-assertions {
  -webkit-align-items: center;
          align-items: center;
  background-color: #fffd;
  background-color: var(--color-white);
  border: 2px solid #204056;
  border: 2px solid var(--color-primary);
  border-radius: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 200px;
          flex-basis: 200px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 8px;
  padding: 17px 32px;
  width: 100%;
}

.box-feedback .box-feedback-assertions p {
  color: #204056;
  color: var(--color-primary);
}

.box-feedback-score {
  padding-right: 8px;
  width: 50%;
}

.box-feedback-button {
  display: -webkit-flex;
  display: flex;
}

.box-feedback-button button:first-child {
  margin-right: 8px;
}

.box-feedback-button button:last-child {
  margin-left: 8px;
}

.trivia-logo {
  display: -webkit-flex;
  display: flex;
  margin: 0 auto 16px;
  max-width: 200px;
}

.flex-block {
  -webkit-flex-flow: column;
          flex-flow: column;
}

.bold {
  font-weight: 700;
}

.btn-config {
  margin-bottom: 20px;
}

.btn-error {
  border: 10px;
  border-radius: 5px;
  font-weight: 800;
  height: 40px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 40px;
  width: 150px;
}

.error {
  background-image: url(../../static/media/erro404.7861302e.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  height: 700px;
  width: 700px;
}

@-webkit-keyframes countdown {

  from {
    stroke-dashoffset: 0;
  }

  to {
    stroke-dashoffset: 113px;
  }
}

@keyframes countdown {

  from {
    stroke-dashoffset: 0;
  }

  to {
    stroke-dashoffset: 113px;
  }
}

